.kontent{
    display:flex;
    flex-direction: row;
    width: 90%;
    margin-left: 5%;
    margin-bottom: 15px;
    margin-top: 40px;
    box-sizing: border-box;
	box-shadow: 11px 13px 7px 0px #78797a;
	background:linear-gradient(to bottom, #fffcfc 5%, #d4d4d4 100%);
	background-color:#fffcfc;
	border-radius:10px;
	border:1px solid #adadad;
	color:#282e30;
	font-family:Arial;
	font-size:17px;
	padding:32px 76px;
	text-decoration:none;
	text-shadow:0px 1px 0px #e1e2ed;
	justify-content: center;
}

.graveyard-map{
	border: solid;
}

.graveyard-map td{
	padding: 15px;
}

.graveyard-map td:hover{
	background-color: rgb(163, 163, 163);
}